*{
	padding: 0;
	margin: 0;
	font-family: 'arial', sans-serif;
	font-size: 16px;
	font-weight: 500;
}

html{
	width:100vw;
	min-height:100vh;
	overflow-x: hidden;

	display: block;
	position: relative; 
}

body{
	width:100%;
	height: 100%;

	padding:20px 0;

	display: block;
	position: relative;

	background-color:#EAEAEA;
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	background-position: center center;
}

#root{
	width:100%;
	height:100%;
	display: block;
	position: relative;
}

.section,
.wrap,
.col,
.row{
	display: block;
	position: relative;
}