.section__wrap{
	width:43%;
	height: 100%;
	color:black;
	margin:0 auto;
	background-color: white;
	box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.35);
	border-radius:10px;
	overflow:hidden;
}

.section__header{
	width:100%;
	padding: 10px 0;
	background: #9C27B0;
	text-align: center;

	p{
		font-size: 2em;
		color:white;
	}

}

.section__form{
	width:94%;
	padding:15px 3%;

	.row{
		width:100%;
	}

	.row__description{
		text-align: center;
		font-size: 0.90em;
		padding:15px 0;
	}

	.wrap__rights{
		width:100%;

		.row__right_item{
			display:flex;
			justify-content: flex-start;
			padding-top:10px;

			&:last-child{
				color:#7b1fa2;
				p{font-weight: bold;}
			}

			p{
				padding-left:3%;
			}
		}
	}

	.row__btn-getToken{
		padding:30px 0;

		.wrap__btn{
			width:100%;
			display:block;
			text-align: center;
			
			a{
				background-color: #7b1fa2;
				color:white;
				padding:10px 5%;
				border-radius: 5px;
				text-decoration:none;
				outline: none;

				&:hover{
					background: #6b0fa1;
				}

			}
		}
	}
}

@media screen and (max-width: 768px){
	
	body{
		padding:0;
	}
	.section__wrap{
		width:70%;
		border-radius:0px;
	}
}


@media screen and (max-width: 480px){
	.section__wrap{
		width:100%;
	}
}
